.Nav{position:fixed;top:0;left:0;width:100%;padding:20px 20px;z-index:999}.Nav #Nav__menu{background-color:rgba(255,255,255,.95);position:fixed;left:0;right:0;top:0;display:flex;justify-content:center;align-items:center;text-align:center;transition:all .5s}.Nav #Nav__menu .Nav__menu__main h1{font-size:4em;margin:25px}.Nav #Nav__menu .Nav__menu__footer{position:fixed;bottom:0;left:0;right:0;justify-content:space-around;display:flex;font-family:"Montserrat Subrayada",sans-serif;margin:10px;font-size:1.5em}.Nav .Nav__menu__open{height:100vh}.Nav .Nav__menu__close{overflow:hidden;height:0;opacity:0%}@media only screen and (min-width: 1000px){.Nav{padding:20px 150px}}.home__icon{z-index:-1;position:absolute;top:0;left:0;right:0;margin:0 auto;text-align:center}.home__icon img{height:calc(50px + (100 - 50) * ((100vw - 300px) / (1600 - 300)))}#nav-icon4{width:calc(1.414213 * calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))));height:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));justify-self:right;transform:rotate(0deg);transition:.5s ease-in-out}#nav-icon4 span{display:block;position:absolute;height:2px;width:100%;background:#2a2a2a;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav-icon4 span:nth-child(1){top:0px;transform-origin:left center}#nav-icon4 span:nth-child(2){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav-icon4 span:nth-child(3){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav-icon4.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav-icon4.open span:nth-child(2){width:0%;opacity:0}#nav-icon4.open span:nth-child(3){transform:rotate(-45deg);top:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));right:0px}
.cursor{width:40px;height:40px;border:2px solid #e08c65;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#e08c65}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#e08c65}html *,body *{cursor:none}
.Footer{background-color:#e08c65}.Footer .contact__description{padding:0;display:flex;flex-direction:column;align-items:flex-end}.Footer .contact__description p{text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(300px + (500 - 300) * ((100vw - 300px) / (1600 - 300)));padding:25px}.Footer .contact__description a{color:#fff}.Footer .contact__description a:hover{color:#2a2a2a}.Footer .copyright{padding:15px;display:flex;justify-content:center}@media only screen and (min-width: 1000px){.Footer .contact__description{padding:50px 150px}}
.About{min-height:100vh;display:flex;justify-content:center;flex-direction:column}.About .About__title{display:flex;flex-direction:column;align-items:center;justify-content:center}.About .About__subtitle{color:rgba(0,0,0,.055);text-align:center;font-size:2.5em}
.Work{padding:0 20px;margin-bottom:50px}.Work h1{text-align:center}.Work h2{text-align:center}.Work .Work__category{display:flex;justify-content:space-around;align-items:center;margin:50px 0}.Work .Work__category svg{height:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));width:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));transition:1s color}.Work .Work__category .active{color:#e08c65}.Work .Work__grid{-webkit-column-count:2;column-count:2;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.Work .Work__grid .Work__item{display:grid;grid-template-rows:1fr auto;margin-bottom:10px;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.Work .Work__grid .Work__item img{width:100%}.Work .Work__grid .Work__item p{padding:.2em .5em}@media only screen and (min-width: 1000px){.Work{padding:0 150px}.Work .Work__grid{-webkit-column-count:3;column-count:3}}
.resume{padding:0 20px;margin-top:75px}.resume h1{text-align:center;font-family:"Montserrat Subrayada",sans-serif;font-size:calc(5em + (10 - 8) * ((100vw - 300px) / (1600 - 300)))}.resume h2{text-align:left;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));margin-bottom:15px}.resume .resume__section__grid{display:grid;grid-template-columns:1fr}.resume .resume__item{padding:0 10px}.resume .resume__item .resume__item__header{position:relative}.resume .resume__item .resume__item__header .resume__item__description__toggle svg{position:absolute;top:0;right:0;height:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)));width:calc(30px + (50 - 30) * ((100vw - 300px) / (1600 - 300)))}.resume .resume__item .resume__item__inner{margin:0 0 0 25px}.resume .resume__item .resume__item__description{transition:.2s max-height;overflow:hidden;padding:0 25px}.resume .resume__item .resume__item__description li{list-style:circle}.resume .resume__item .resume__item__description__show{max-height:500px}.resume .resume__item .resume__item__description__hidden{max-height:0}@media only screen and (min-width: 1000px){.resume{padding:0 150px}.resume .resume__section__grid{grid-template-columns:1fr 1fr}}
.work__page{padding:0 20px;margin:75px 0}.work__page h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(5em + (10 - 8) * ((100vw - 300px) / (1600 - 300)))}.work__page .work__page__content .temp_images{width:100%}.work__page .work__page__content .work__text p{margin-top:10px}.work__page .back__button{margin-top:50px}@media only screen and (min-width: 1000px){.work__page{padding:0 150px}}

